
.home_container {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
}

.home_container .audio {
    
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.home_container .rhap_container  {
    width: 100vw;
}

.home_container h1 {
    width: 100%;
    padding: 10px;
    height: 55px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    background-color: gray;
    color: whitesmoke;
    box-sizing: border-box;
}