.login_container {
    width: 100vw;
    height: 100vh;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.login_container h1 {
    color: rgb(73, 73, 73);
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 25px;
    margin: 10px;
}

.login_container input {
    width: 250px;
    height: 35px;
    font-size: 15px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding: 10px;
    border: solid 0.5px rgb(41, 41, 41);
    border-radius: 10px;
    box-shadow: 1px 1px 15px 2px rgb(95, 95, 95);
    margin: 10px;
    direction: ltr;
    text-align: right;
    margin-top: 30px;
}